import moment, { now } from 'moment';
import React, { useEffect, useState } from 'react'
import rainImage from '../img/rain.jpg'
import snowImage from '../img/snow.jpg'
import sunImage from '../img/sun.jpg'
import cloudImage from '../img/cloud.jpg'
import NextDays from './NextDays';

function InfoCity(props) {

    const { cityData, nextDaysData } = props;

    const [nowTime, setNowTime] = useState(moment().format('h:mm:ss a'))
    const [bgImage, setbgImage] = useState('')

    useEffect(()=>{
        setTimeout(()=>{
            setNowTime(moment().format('h:mm:ss a'))
        },1000)
    },[nowTime])


    const weatherImage = async() =>{
        const weather = cityData.weather.map(city =>{
            return city.main
        })
        const state = weather.toString()
        if(state === 'Snow' || state === 'Squall'){
            setbgImage(snowImage) 
        }else if(state.includes('Mist','Rain','Smoke','Haze','Fog','Tornado')){
            setbgImage(rainImage)
        }else if(state === 'Clouds'){
            setbgImage(cloudImage)
        }else if(state === 'Clear'){
            setbgImage(sunImage)
        }else if(state.includes('Haze','Dust','Ash','Sand')){

        }
    }

    useEffect(()=>{
        weatherImage();
    },[cityData])


    const date = moment().format('MMMM Do YYYY')


    const convertDeg = (deg) =>{
        let pre = parseInt(deg) ;
        return pre.toFixed(0)
    }

  return (
    <>
        {!cityData
        ? 
        <h1>No existe Ciudad</h1>
        :
        
            <div className='dataCity container'>

                <picture className='bgWeather reveal'>
                    <img src={ bgImage } alt="" />
                </picture>

                <div className='cityHero'>
                    <div className='cityMain'>

                        <div className='cityHead'>
                            <h1>{`${cityData.name}, ${cityData.sys.country}`}</h1>
                            <p className='time'>{date}</p>
                        </div>

                        <div className='cityWeather'>
                            <div className='weatherDay'>
                                <p className='todayDeg'>
                                    {convertDeg(cityData.main.temp)}<sup>c</sup>
                                </p>
                                <div className='todayImage'>     
                                    <img 
                                        src={`http://openweathermap.org/img/wn/${cityData.weather[0].icon}@2x.png`} 
                                        alt="" 
                                    />
                                </div>
                            </div>
                            <p className='todayNameWeather'>
                                    {cityData.weather[0].description}
                            </p>
                        </div>         
                            
                    </div>

                    <div className='weather'>
                        <p className='dayName'>Hoy</p>
                        <div className='weatherInfo'>
                            <div className='contentInfoToday'>
                                <p className='headInfo'>Detalles del Clima</p>
                                <div className='itemInfo'>
                                    <p>
                                        Max:
                                    </p>
                                    <p>
                                        {convertDeg(cityData.main.temp_max)}<sup>c</sup>
                                    </p>
                                </div>
                                <div className='itemInfo'>
                                    <p>Min:</p>
                                    <p>
                                        {convertDeg(cityData.main.temp_min)}<sup>c</sup>
                                    </p>
                                </div>
                                <div className='itemInfo'>
                                    <p>Humedad:</p>
                                    <p>
                                        {convertDeg(cityData.main.humidity)}%
                                    </p>
                                </div>
                                <div className='itemInfo'>
                                    <p>Direccin del viento:</p>
                                    <p>
                                        {convertDeg(cityData.wind.deg)}
                                    </p>
                                </div>
                                <div className='itemInfo'>
                                    <p>Velocidad de viento:</p>
                                    <p>
                                        {convertDeg(cityData.wind.speed)} km/h
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className='hour'>
                    <p>{nowTime}</p>
                </div>
                
                <NextDays nextDays = {nextDaysData} nowTime = {nowTime} />

            </div>
        
        }
    </>
  )
}

export default InfoCity