import React, { useEffect, useState } from 'react';
import './App.css';
import Header from './components/Header';
import { getCity, getDataCity, getCurrentCity, getNextDays} from './Api/Api'
import InfoCity from './components/InfoCity';
import customBg from './img/customBg.jpg'


function App() {

  const [cityInfo, setCityInfo] = useState();
  const [noExist, setNoExist] = useState(false);
  const [nextDays, setNextDays] = useState();

  const getCurrentLocation = async() =>{
    if ("geolocation" in navigator) {
      navigator.geolocation.getCurrentPosition(function(position) {
        let lat = position.coords.latitude
        let lon = position.coords.longitude
        fetchCustomCity(lat, lon);
      });      
    } else {
      console.log('No se permito la localizacion')
    }
  }

  

  const fetchCustomCity = async(lat, lon) =>{
    if(lat && lon){
      try {
        const currentCityData = await getCurrentCity(lat, lon)
        const nextDaysData = await getNextDays(currentCityData.id)
        setCityInfo(currentCityData)
        setNextDays(nextDaysData)
      } 
      catch (error) {
        console.log(error)
      }
     
    }
  }

  useEffect(()=>{
    getCurrentLocation()
  },[])


  

  const onSearch = async(city)=>{
    
    if(city === ''){
      setNoExist(false)
      getCurrentLocation();
    }
    else{
      try {
        const data = await getCity(city)
        if(data.cod === '404'){
          setNoExist(true)
          getCurrentLocation();
        }else{
          const nextDaysData = await getNextDays(data.id)
          setNextDays(nextDaysData)
          setCityInfo(data)
        }
      } catch (error) {
        console.log(error)
      }
    }
    
  }


  return (
    <div className="App">
      <picture className='bgWeather customBg'>
        <img src={customBg} alt="" />
      </picture>
      <Header 
        onSearch={onSearch} 
        currentLocation={getCurrentLocation}
        noExist={noExist}
      />
      {!cityInfo ?
        <div className='loading'>
          <h1><i className='bx bx-loader-circle bx-spin' ></i> Loading...</h1>
          <p>(Please allow the access to your location in the website)</p>
        </div>
        :
        <InfoCity cityData={cityInfo} nextDaysData={nextDays} />
      }
    </div>
  );
}

export default App;
