
const cors = require('cors')
const apiKey = 'bf908d7f1299cdbef05413223d991230'

export const getCity = async (city) =>{
    let url = `https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=${apiKey}&lang=es&units=metric`
    try {
        const response = await fetch(url);
        const data = await response.json();
        return data;

    } catch (error) {
    }
}
export const getDataCity = async (id) =>{
    try {
        let url = `https://api.openweathermap.org/data/2.5/weather?id=${id}&appid=${apiKey}`
        const response = await fetch(url);
        const data = await response.json();
        return data;

    } catch (error) {
        console.log(error)
    }
}
export const getCurrentCity = async (lat, long) =>{
    try {
        let url = `https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${long}&appid=${apiKey}&lang=es&units=metric`

        const response = await fetch(url);
        const data = await response.json();
        return data;

    } catch (error) {
        console.log(error)
    }
}
export const getNextDays = async (idCity) =>{
    try {
        let url = `https://api.openweathermap.org/data/2.5/forecast?id=${idCity}&appid=${apiKey}&lang=es&units=metric`
        const response = await fetch(url);
        const data = await response.json();
        return data.list;

    } catch (error) {
        console.log(error)
    }
}

