import moment from 'moment';
import React from 'react'
import { useEffect, useState } from 'react'
import 'moment/locale/es';


function NextDays(props) {

    const { nextDays, nowTime } = props
    const [weekData, setWeekData] = useState([])

    const convertDeg = (deg) =>{
        let pre = parseInt(deg) ;
        return pre.toFixed(0)
    }

    const getDataAtTime = () =>{
        
       const arrayWeek = []
       const tempMax = []

       if(nextDays){
           nextDays.forEach(day => {
               let hora = moment(day.dt_txt).format('h:mm a');
               let dia = moment(day.dt_txt).format('dddd')
               let temp = day.main.temp
               let icon = day.weather[0].icon
               let description = day.weather[0].description
               if(hora === '3:00 am'){
                   let dayInfo = {
                       dia: dia,
                       hora: hora,
                       icon: icon,
                       min: temp,
                       max:'',
                       description: description
                   }
                   arrayWeek.push(dayInfo)
               }
               if(hora === '9:00 pm'){
                   tempMax.push(temp)
               }
           });
        }
        for (let i = 0; i < arrayWeek.length; i++) {
            arrayWeek[i].max = tempMax[i]
        }
        setWeekData(arrayWeek)
    }

    useEffect(() => {
      getDataAtTime();
    }, [nextDays])
    

  return (
    <div className='nextDays'>

        {
            weekData ? 
            weekData.map((day,idx)=>{
                return(
                    <div className='day' key={idx}>
                                    <header className='dayName'>
                                        {day.dia}
                                    </header>
                                    <div className='weatherDay'>
                                        <img src={`http://openweathermap.org/img/wn/${day.icon}@2x.png`} alt="" />
                                        <div>
                                            <p className='degDay'>
                                                {convertDeg(day.max)}<sup>c</sup>
                                            </p>
                                            <p className='degDay minDeg'>
                                                {convertDeg(day.min)}<sup>c</sup>
                                            </p>
                                        </div>
                                    </div>

                                    <p className='kindWeather'>
                                        {day.description}
                                    </p>
                                    
                                    
                    </div>    
                )
            })
            : 
            'No hay' 
        }

                       
    </div>
  )
}

export default NextDays